import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import {MediaHomeComponent} from './media-home-component';
import {MediaAboutComponent} from './media-about-component';

const routes: Routes = [

    {path:'mediahome', component:MediaHomeComponent},
    {path:'mediaabout', component:MediaAboutComponent}
];
    
@NgModule({
    imports:[
        RouterModule.forRoot(routes)
    ],
    exports :[
        RouterModule
    ]

})

export class mediaRoutingComponents
{

}
export const routingComponents = [MediaHomeComponent,MediaAboutComponent]


