import { Component } from '@angular/core';
import { BasicService } from './Basic.service';

@Component({
  selector: 'media-about',
  templateUrl: './media-about.component.html',
  styleUrls: ['./media-about.component.css']
})
export class   MediaAboutComponent{


  name: string;
  data: any;
  constructor(private MyService: BasicService) {
    this.name = "Angular Service";

      this.MyService.GetPosts().subscribe(posts => { this.data = posts });
  }
}



