import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { mediaRoutingComponents } from './media-routing-component';
import { AppComponent } from './app.component';
import {MediaItemComponent} from './media-item.component';
import { routingComponents } from './media-routing-component';
import { MediaItemListComponent } from './media-item-list.component';
import { MediaItemFormComponent } from './media-item-form.component';
import {MediaItemService} from './media-item.service';
import {BasicService} from './Basic.service';

@NgModule({
  declarations: [
    AppComponent, MediaItemComponent,MediaItemListComponent,MediaItemFormComponent,
    routingComponents
  ],
  imports: [
    BrowserModule,
    mediaRoutingComponents,
    ReactiveFormsModule,
    HttpModule
  ],
  providers:[MediaItemService,BasicService],
  bootstrap: [AppComponent]
})
export class AppModule { }



